.input {
  display: block;
  box-sizing: border-box;
  width: 100%;
  height: 40px;
  margin-bottom: 8px;
  padding: 0;
  padding-bottom: 18px;

  background: none;
  border: none;
  border-bottom: 1px solid $button-color;

  color: $header-text-color;
  transition: border-color 0.3s;

  &--last {
    margin-bottom: 30px;

    @include onIPad {
      margin-bottom: 48px;
    }

    @include onDesktop {
      margin-bottom: 90px;
    }
  }

  &--textarea {
    height: 108px;

    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  &--invalid {
    border-color: $inputInvalid;
  }

  &::placeholder {
    font-family: Mark, sans-serif;
    font-size: 14px;
    line-height: 20px;
    color: $text-color;
  }

  &:focus {
    outline: none;
    border-color: $color-blue;
  }
}
